         <p> Die Dokumente der Rechtsakten aus der Kanzlei Oskar Samek enthalten unterschiedliche Textarten, weshalb fr die verschiedenen Textgruppen auch unterschiedliche Editionsprinzipien aufgestellt wurden, die hier mitgeteilt werden. Auerdem bestehen die Akten aus verschiedenen Materialittstypen, wobei der Groteil als Typoskripte berliefert ist. Darber hinaus finden sich Handschriften sowie zahlreiche maschinen- und handschriftlich ausgefllte vorbedruckte Formulare.</p>
