<p class="font-weight-bold">Grundlage der edierten Dokumente</p>

<p>Als Textgrundlage wurde generell das verffentlichte bzw. abgeschickte Dokument verwendet. Meistens liegt in den Rechtsakten nur bei den empfangenen Dokumenten der gegnerischen Seite bzw. der Gerichte das tatschlich abgeschickte Dokument vor. Aus der Samek-Kanzlei sind in der Regel nur die Durchschlge der abgeschickten Dokumente, mitunter auch nur Vorstufen bzw. Fassungen, berliefert. Aus diesem Grund konnte nicht immer mit Sicherheit festgestellt werden, welcher Brief, welche Klage- oder Berufungsschrift tatschlich abgeschickt wurde. Liegt das Dokument in mehreren, voneinander abweichenden Exemplaren vor, wurden die Varianten ediert, da diese den mitunter aufwendigen Arbeitsprozess und das Zwiegesprch zwischen Anwalt und Klient abbilden knnen. In diesen Fllen bildet die Textgrundlage dasjenige Exemplar, das den weitest fortgeschrittenen Arbeitsprozess darstellt. Auf Differenzen wird im Variantenapparat verwiesen. Sind die Textunterschiede zweier Exemplare erheblich, wurden beide Textzeugen ediert. </p>
<p>Dokumentabschriften sind nur dann ediert, wenn sie das einzige berlieferte Exemplar darstellen. Dabei werden Schreib- bzw. Tippfehler ignoriert, da nicht die Fehleranflligkeit der Schreiber:innen dokumentiert werden sollte. Gleiches Verfahren wurde bei den juristischen Dokumenten aus den Gerichten, etwa bei Urteilsschriften, angewendet, da hier kein Autor, wie es bspw. bei Briefen der Fall ist, zu bestimmen war, und diese Edition primr den Informationsgehalt des Urteils vermitteln soll.</p>
<p>Innerhalb der Projektlaufzeit blieb nach Abschluss der Kodierung lediglich Zeit fr einen Lektorats- bzw. Korrektoratsdurchgang.</p>


<p class="font-weight-bold">Allgemeine Transkriptionsregeln</p>

<p>Die Dokumente wurden zeilengenau transkribiert. Bei mehrspaltigem Satz werden die Spalteninhalte durch Abstze voneinander getrennt.</p>
<p>Spte nderungen, die oft handschriftlich erfolgt sind, wurden stets bercksichtigt, da sie die Korrekturablufe transparent machen und zeigen, dass Anwalt und Mandant, mitunter auch sehr intensiv, an den Texten im Nachhinein gearbeitet haben. Tippfehler, die sofort gendert wurden, sind hingegen nicht ediert, da nicht die Schreibfhigkeit der Schreiber:innen dokumentiert werden soll. Falsche Schreibweisen von Namen wurden allerdings konsequent wiedergegeben. Grundstzlich wurden nur den Wortlaut betreffende sowie sptere handschriftliche interpunktionelle und orthografische nderungen wiedergegeben, Sofortnderungen der Orthografie und Interpunktion wurden hingegen nicht aufgenommen.</p>
<p>Der Briefkopf in Korrespondenzen (der neben dem Datum, den Adressen der am Schriftstck beteiligten Personen ebenso einen Betreff und eine Diktatsigle enthalten kann) wurde in den Metadaten erfasst, nicht aber transkribiert. Stempel der gerichtlichen Dokumente wurden nicht transkribiert, aber in den Metadaten erfasst.</p>
<p>Sperrungen und Unterstreichungen sind grundstzlich als solche differenziert wiedergegeben, weil beide Hervorhebungen mitunter auch im selben Exemplar vorkommen knnen. Anfhrungszeichen sind einheitlich und typografisch korrekt (also nicht mimetisch) wiedergegeben, um die Lesbarkeit der Dokumente, in denen hufig zitiert wurde, zu gewhrleisten. Auch die zum Teil gebruchliche Schreibweise bei Preisangaben durch zwei Bindestriche wurde durch ein Halbgeviert angepasst. Verschleifungen bei Unterschriften wurden aufgelst. Seiten- und Blattzhlungen, Dittografien und Kustoden werden nicht wiedergegeben. Spatien zwischen Abkrzungen wurden einheitlich nicht gesetzt. Unvollstndige Datumsangaben bzw. Teile davon wurden ergnzt, da diese bei Durchschlgen oft fehlen, auf dem Typoskript allerdings auf dem vorbedruckten Briefpapier vorhanden waren. Eigentmliche Schreibweisen wie bspw. die Verwendung eines Versal-J anstelle des Versal-I wurden normalisiert. Editorische Hinzufgungen wurden mit eckigen Klammern gekennzeichnet. Fr unleserliche Buchstaben und Wrter sind (mehrere) [] gesetzt.</p>


<p class="font-weight-bold">Schreibmaterial</p>

<p>Wurde in einem maschinell erstellten Dokument handschriftlich gendert, so ist dieser Wechsel des Schreibmaterials in der Transkription dargestellt sowie in den Metadaten genauer beschrieben, indem Schreiberhnde und Schreibmaterial mitgeteilt sind. Bei nachtrglichen handschriftlichen Hinzufgungen am Dokument wurde auerdem in der Darstellung unterschieden, ob es sich um eine handschriftliche Notiz bzw. Hervorhebung durch Marginalien am Rande handelt oder ob tatschlich eine nderung am Text vorgenommen wurde. Handschriftlich gesetzte Unterschriften wurden nicht als solche extra ausgezeichnet. Sogenannte Kanzleivermerke, die in der Regel ebenfalls handschriftlich und meistens am Ende eines Dokuments angebracht wurden, sind einheitlich differenziert in der Transkription wiedergegeben.</p>

<p>Durch die synoptische Darstellung ist die Transkription an jeder Stelle mit dem Original berprfbar.</p>

<p class="font-weight-bold">Nicht transkribierte Dokumente und Akten</p>

<p>Smtliche Zeitungstexte, Originalmappen, -umschlge und Aktenvermerke sowie Kostenbestimmungen, Rckscheine, Rechnungen, Ladungen und weitere vorbedruckte Dokumente wurden nicht transkribiert, sondern mittels Faksimile dargestellt und durch die Angabe ihrer Metadaten erfasst. Die von Kraus und Samek angelegten Aktensammlungen zu Emmerich Bkessy (Akten 4359) wurden nicht ediert, da es sich hier meistens um Materialien zu Prozessen anderer Anwlte und ihrer Klienten handelt. Die Akten 204215, bei denen es sich primr um separat abgelegte Kontextmaterialien handelt, wurden ebenfalls nicht ediert.</p>

<p class="font-weight-bold">Materialitts- und Dokumentstypen</p>

